.carouselContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 80vh;
  }

  #title{
      color: aliceblue;
      padding-left: 80px;
  }

  .carousel{
      height: 90%;
      width: 75%;
      padding: 20px 20px 0px 20px;
      background-color:aliceblue;
      border-radius: 20px;
  }

  .listOfProjects{
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      margin: 0px;
  }
